// border: 2px solid rgb(255, 0, 212);


@media (min-width:1440px) {
    .body{
        background-image: url(../img/desktop/index-background-desktop.png);
        background-repeat: no-repeat;
        background-position: top right;
        background-size: 720px 696px;
    }
}


.head-block {
    background-image: url(../img/mobile/background-mobile.png);
    background-repeat: no-repeat;
    background-position-x: center;
    width:100%;
}
@media (min-width:768px) {
    .head-block{
        background-image: none;
        margin: 0 auto;
        // border: 2px solid rgb(255, 0, 212);
    }
}


.head-block__wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: 710px;
    padding-bottom: 42px;

    color: #fff;
    @extend %oswald-400;
}
@media (min-width:768px){
    .head-block__wrapper{
        align-items: flex-start;
        padding-bottom: 0px;
    }
}
@media (min-width:1440px) {  
    .head-block__wrapper{
        max-width: 1220px;
        flex-direction: row;
        justify-content: flex-start;
        padding-bottom: 84px;
    }
}


.head-block__text-block{
    text-align: center;
    max-width: 280px;
    position: relative;
}
@media (min-width:768px){
    .head-block__text-block{
        text-align: start;
        color: #111111;
        max-width: 430px;
        margin-left: 90px;
    }
}
@media (min-width:1440px){
    .head-block__text-block{
        margin-left: 0;
    }
}


.head-block__title{
    font-size: 36px;
    line-height: 36px;
    padding: 29px 0 20px 0;
}
@media (min-width:768px) {
    .head-block__title{
        font-size: 60px;
        line-height: 60px;
        padding: 62px 0 33px 0;
    }
}
@media (min-width:1440px) {
    .head-block__title{
        padding: 105px 0 33px 0;
    }
}


.head-block__text{
    font-size: 14px;
    line-height: 14px;
    text-transform: uppercase;
    padding: 0 0 29px 0;
}
@media (min-width:768px) {
    .head-block__text{
        font-size: 20px;
        line-height: 20px;
        text-transform: uppercase;
        padding: 0 0 20px 0;
    }
}
@media (min-width:1440px) {
}


.head-block__btn{
    width: 280px;
    height: 40px;

    font-size: 14px;
    line-height: 18px;
    padding-top: 12px;

    position: absolute;
    top: 440px;
    left: 0;
    right: 0;
    margin: auto;
}
@media (min-width:768px) {
    .head-block__btn{
        width: 245px;
        height: 52px;

        font-size: 20px;
        line-height: 20px;
        padding-top: 17px;
        margin: 0;
        top: 283px;
        left: 0px;
    }
}
@media (min-width:1440px) {
    .head-block__btn{
        top: 313px;
        left: 0px;
    }
}





































