.example {
    background-color: #f2f2f2;
}
@media(min-width: 768px){
    .example{
        background-color: inherit;
    }
}


.example__inner {
    padding: 21px 0px 38px;
}
@media(min-width: 768px){
    .example__inner {
        padding: 51px 0px 67px;
    }
}


.example__content {
    margin: 0 auto;
    max-width: 280px;
    // border: 1px solid rgb(234, 5, 5);
}
@media(min-width: 768px){
    .example__content {
        margin: 0 auto;
        max-width: 710px;    
    }
}


.example__title {
    color: #111111;
    @extend %oswald-400;
    font-size: 36px;
    line-height: 60px;
    text-align: left;
    padding-bottom: 21px;
}
@media(min-width: 768px){
    .example__title {
        font-size: 60px;
        padding-bottom: 60px;
    }
}


.example__text {
    font-size: 14px;
    line-height: 18px;
    max-width: 266px;
    text-align: left;
    padding-bottom: 27px;
}
@media(min-width: 768px){
    .example__text {
        font-size: 16px;
        line-height: 24px;
        max-width: 676px;
        padding-bottom: 56px;
    }  
}


.example__table{
    // border: 1px solid rgb(234, 5, 5);
}
@media(min-width: 768px){
    .example__table{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 86px;
    }
}


.table {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 38px;
}
@media(min-width: 768px){
    .table {
        justify-content: flex-start;
        margin-bottom: 0;
    } 
}


.table__item {
    width: 124px;
    height: 56px;
    border: 1px solid #cdcdcd;
    border-radius: 3px;
    text-align: center;
    position: relative;
}
@media(min-width: 768px){
    .table__item {
        width: 162px;
        height: 72px;
    }
    .table__item:not(:last-child){
        margin-right: 60px;
    }
}


.table__value {
    @extend %oswald-400;
    font-size: 24px;
    line-height: 24px;
    color: #111111;
    padding: 12px 0 9px;
}
@media(min-width: 768px){
    .table__value {
        font-size: 30px;
        line-height: 37.02px;
        padding: 13px 0 10px;
    }
}


.table__mark {
    font-size: 12px;
    line-height: 12px;
    text-transform: lowercase;
    min-width: 80px;
    position: absolute;
    bottom: -15px;
    transform: translateX(-50%);
    left: 50%;
    background-color: #f2f2f2;
    padding: 0 9px;
}
@media(min-width: 768px){
    .table__mark {
        font-size: 14px;
        line-height: 14px;
        bottom: -7px;
        white-space: nowrap;
        background-color: #ffffff;
    }
}


.table__discr {}  //- может удалить потом


.table__discr-text{
    @extend %oswald-400;
    font-size: 14px;
    line-height: 20px;
    color: #111111;
    text-align: center;
    text-transform: uppercase;
}
@media(min-width: 768px){
    .table__discr-text{
        font-size: 20px;
        line-height: 28px;
        text-align: right;
        max-width: 175px;
    }
}



//slider

.slider {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;

}
@media(min-width: 768px){
    .slider {
        // background-image:linear-gradient(#fff 169px,#f2f2f2 169px,#f2f2f2)

        // background-image:linear-gradient(#fff,#fff 213px,#f2f2f2 213px,#f2f2f2)
    }
}

.example__slider{}

.slider__grey-block{
    
    background-image: linear-gradient(to left, #f2f2f2 50%, #eaeaea 50%);
}


.slider__picture {
    position: relative;
    width: 311px;
    height: 240px;
    overflow: hidden;
    margin: 35px 0;
    left: 0;
}
@media(min-width: 768px){
    .slider__picture {
        width: 677px;
        height: 517px;
        margin: 0 0 13px 0;
    }
}


.slider__img-before{
    position: absolute;
    display: block;
    left: 0;
    top: 8px;
    z-index: 1;
    clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
}
.slider__img-after{
    position: absolute;
    display: block;
    left: 40px;
    top: 0;
    clip-path: polygon(35% 100%, 54% 100%, 100% 100%, 50% -35%);
}
@media(min-width: 768px){
    .slider__img-before{
        top: 25px;
    }
    .slider__img-after{
        left: 91px;
        top: 0;
    }
}


// toggle
// @media(min-width: 768px){}


.slider__toggle{
    // border: 1px solid rgb(54, 228, 219);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 197px;
}
@media(min-width: 768px){
    .slider__toggle{
        width: 569px;
    }
}


.slider__link{
    @extend %oswald-400;
    text-transform: uppercase;
    color: #111111;
    font-size: 16px;
    line-height: 20px;
}
@media(min-width: 768px){
    .slider__link{
        font-size: 20px;
    }
}


.slider__scale{
    width: 84px;
    height: 14px;
    border: 1px solid #cdcdcd;
    background-color: #ffffff;
    border-radius: 6px;
}
@media(min-width: 768px){
    .slider__scale{
        width: 428px;
        height: 6px;
        background-color: #dcdcdc;
        border-radius: 3px;
        margin: 0 21px;
    }
}


.slider__grip{
    border-radius: 3px;
    width: 35px;
    height: 6px;
    background-color: #68b738;
    margin: 3px 0 0 3px;
}
@media(min-width: 768px){
    .slider__grip{
        border-radius: 50%;
        width: 34px;
        height: 34px;
        background-image: radial-gradient(#68b738 0,#68b738 4px,#fff 4px,#fff 100%);
        margin: -15px 0 0 197px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    }
}

