@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  src: url(../fonts/oswald-regular.woff2), url(../fonts/oswald-regular.woff);
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  src: url(../fonts/arial-regular.woff2), url(../fonts/arial-regular.woff);
  font-weight: 400;
  font-style: normal;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

input, button {
  font-family: inherit;
  font-size: inherit;
  border: none;
}

button {
  background: none;
  cursor: pointer;
}

input:focus, button:focus {
  outline: none;
}

ul, li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

html, body {
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0px 20px 0px 20px;
  border: 1px solid rgb(0, 0, 0);
}

@media (min-width: 768px) {
