
.how-it-works{
    padding-bottom: 42px;
    
    &__inner{
        max-width: 275px;
        margin: 0 auto;
    }
    &__title{
        @extend %oswald-400;
        font-size: 36px;
        line-height: 60px;
        color: #111111;
        margin-bottom: 35px;
    }
    &__items{
        display: flex;
        flex-direction: column;
    }
    &__item{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        &::before{
            content: "";
            display: block;
            width: 60px;
            height: 60px;
            background-color: $bg-color;
            flex-shrink: 0;
            margin-right: 20px;
            z-index: -1;
            background-repeat: no-repeat;
            background-position: center;
        } 
    }   
    &__item--list::before{
        background-image: url(../img/mobile/advantage-01.png);
    }
    &__item--wash::before{
        background-image: url(../img/mobile/advantage-02.png);
    }
    &__item--fork::before{
        background-image: url(../img/mobile/advantage-03.png);
    }
    &__item--clock::before{
        background-image: url(../img/mobile/advantage-04.png);
    }

    &__text{
        line-height: 18px;
    }
}





@media(min-width:768px){
    .how-it-works{
        // padding-bottom: 51px;
        border-bottom:1px solid #e6e6e6;
        &__inner{
            max-width: 708px;
        }
        &__title{
            font-size: 60px;
            margin-bottom: 63px;
        }
        &__items{                 
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            // border: 1px solid rgb(228, 131, 92);

        }
        &__item{
            // border: 1px solid rgb(228, 92, 212);
            position: relative;
            width: 50%;
            max-width: 245px;
            min-height: 230px;
            overflow: hidden;
            
            display: flex;
            flex-direction: column;
            // justify-content: flex-start;
            align-items: flex-start;
            margin: 0 0 80px 0;
            &:last-child{
                margin-bottom: 80px;    
            }
            &:nth-child(odd){
                margin-right: 147px;
            }


            &::before{
                content: "";
                display: block;
                width: 80px;
                height: 80px;
                margin: 0 46px 37px 0;
            } 
            &::after{
                // border: 1px solid rgb(92, 228, 92);
                display: block;
                font-family: 'Oswald';
                color: #f2f2f2;
                font-size: 281px;
                line-height: 202.07px;

                position: absolute;
                // top: -40px;
                top: 1px;
                z-index: -1;
            }
        }   
        &__item--list::after{
            content: "1";
            right: 16px;
        }
        &__item--wash::after{
            content: "2";
            right: -6px;
        }
        &__item--fork::after{
            content: "3";
            right: -5px;
        }
        &__item--clock::after{
            content: "4";
            right: -9px;
        }
    
        &__text{
            font-size: 16px;
            line-height: 24px;
        }
    }
}





